import url from '@/request/url.js'
import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			plans: []
		}
	},
	computed: {
		...mapState({
			userInfo: state => state.userInfo
		})
	},
	methods: {
		getAllPlan(callback) {
			uni.request({
				url: url.getAllPlan,
				method: "GET",
				data: {
					uid: this.userInfo.id,
					isAll: true
				},
				success: callback
			});
		}
	},
	onLoad() {
		this.getAllPlan(resp => {
			console.log(resp.data);
			resp.data.forEach(item => {
				let tmp = this.plans.find((val, index, arr) => val.day == item.createDate);
				if (tmp) {
					tmp.data.push(item);
				} else {
					this.plans.push({
						day: item.createDate,
						data: [item]
					});
				}
			});
		});
		console.log(this.plans);
	}
}
